import global from "../../../utils/global.js";
import babyApi from "../../../service/baby";

export default {
	props:["babyList"],
	data() {
		return {
			statusBarHeight: 0, 
			titleBarHeight: 0, 
			show: false,
			radio: '0',
		}
	},
	created() {
		var  that = this;
		uni.getSystemInfo({
			success: function(res) {
				if (res.model.indexOf('iPhone') !== -1) {
					that.titleBarHeight = 44 + 'px';
				} else {
					that.titleBarHeight = 48  + 'px';
				}
					that.statusBarHeight = res.statusBarHeight  + 'px'
			},
				
		})
	 },
	onLoad(options) {
	 	if(global.getBabyId()){
			this.radio = global.getBabyId().toString();
		}else{
			this.radio = '0';
		}
	},
	methods: {
		onChange(event) {
			this.radio = event.detail;
			if(this.radio!=0){
				this.baby = this.babyList.filter(item => {
				    return item.id == this.radio
				});
				global.setBabyBirthday(this.baby[0].birthday);
				global.setBabyId(this.radio);
			}else{
				global.removeBabyBirthday();
				global.removeBabyId();
			}
			if (getCurrentPages().length != 0) {
				//刷新当前页面的数据
				getCurrentPages()[getCurrentPages().length - 1].onLoad()
			}
			this.show = false;
		},
		onClose() {
			this.show = false;
		},
		showPopup(type) {
			this.show = true;
			if(global.getBabyId()){
				this.radio = global.getBabyId().toString();
			}else{
				this.radio = '0'
			}
		},
		addBaby(){
			this.show = false;
			uni.navigateTo({
			    url:'/pages/baby/addBaby'
			});
		},
		tomyBaby(){
			this.show = false;
			uni.navigateTo({
			    url:'/pages/my/myBaby'
			});
		},
		// 回到上一页
		back: function(){
			 uni.navigateBack({
			 	delta:1
			 })
		},
	}
}
