import LoginIn from './component/Login.vue';
import Nobaby from './component/Nobaby.vue';
import TaskBar from './component/TaskBar.vue';
import authApi from '../../service/auth';
import babyApi from "../../service/baby";

export default {
	components:{
		LoginIn,
		Nobaby,
		TaskBar
	},
	data(){
		return{
			isLogin:'',
			babyList: [],
			listShow: 1,
		}
	},
	methods:{
		//用户登录成功后执行的操作
		//列表
		getbabyList(){
		 	babyApi.getBabyList().then(res=>{
		 		if(res.code == "0000"){
		 			this.babyList = res.data;
		 		}else{
		 			uni.showToast({
		 				icon:"none",
		 				title: res.responseMsg
		 			});
		 		}
		 	});
		},
		toList(type){
			if(type==1){
				this.listShow = 1
			}else if(type==2){
				this.listShow = 2
			}else if(type==3){
				this.listShow = 3
			}
		}
	},
	async onShow(options){
	  const login = await authApi.login();
	  const isHaveBaby = await babyApi.isHaveBaby();
	  if(!login){
		this.isLogin = 1;
	  }else if(!isHaveBaby){
		this.isLogin = 2; 
	  }else{
		this.isLogin = 3;
		this.getbabyList();
	  }
	}
}
